import React from 'react';
import { NavLink } from 'react-router-dom';
import web from '../images/img1.svg'

function Common(props) {
    return (
        <div>
          <section id="header" className="d-flex align-items-center">
          <div className="container-fluid nav_bg">
                <div className="row">
                    <div className="col-10 mx-auto">
                    <div className="row">
                        <div className="col-md-6 col-12 pt-5 pt-lg-0 order-2 order-lg-1 d-flex align-items-center">
                        <div>
                        <h1>{props.name}<strong className="brand_name">Thapa Technical</strong></h1>
                        <h2 className="my-3">
                        We are the team of talented developer making website
                        </h2>
                        <div className="mt-3">
                            <NavLink to={props.visit} className="btn get_started">{props.btnname}</NavLink>
                        </div>
                        </div>
                        </div>
                        <div className="col-lg-6 pt-5 pt-lg-2 order-1 order-lg-2 text-right">
                       <img src={props.imgsrc} className="img-fluid animated" alt="img" />
                        </div>
                        </div>
                    </div>
                </div>
            </div>

          </section>
        </div>
    )
}

export default Common
